import React, { useEffect } from 'react';
import {
  LoginWrap,
  LoginTitle,
  SignUpLink,
  SimpleLoginWrap,
  SnsButton,
  SnsList,
} from './StyledLogin';
import LoginForm from '../../components/Login/LoginForm';
import sprite from '../../images/SpriteIcon.svg';
import { useNavigate } from 'react-router-dom';

export default function Login() {
  const SnsSVG = ({ id, size = 24 }) => (
    <svg width={size} height={size}>
      <use href={`${sprite}#${id}`} />
    </svg>
  );

  const navigate = useNavigate();

  useEffect(() => {
    if (
      sessionStorage.getItem('_id') ||
      sessionStorage.getItem('accountname') ||
      sessionStorage.getItem('token')
    ) {
      navigate('/home');
    }
  }, [navigate]);

  return (
    <>
      <h1 className='a11y-hidden'> </h1>
      <LoginWrap>
        <LoginTitle> !</LoginTitle>
        <LoginForm />
        <SignUpLink to='/signup'>  ?</SignUpLink>
        <SimpleLoginWrap data-content=' '>
          <h2 className='a11y-hidden'> </h2>
          <SnsList>
            <li>
              <SnsButton
                $color='kakao'
                type='button'
                title='  '
              >
                <SnsSVG id='kakao' />
                <h3 className='a11y-hidden'>  </h3>
              </SnsButton>
            </li>
            <li>
              <SnsButton
                $color='google'
                type='button'
                title='  '
              >
                <SnsSVG id='google' />
                <h3 className='a11y-hidden'>  </h3>
              </SnsButton>
            </li>
            <li>
              <SnsButton
                $color='github'
                type='button'
                title='  '
              >
                <SnsSVG id='github' />
                <h3 className='a11y-hidden'>  </h3>
              </SnsButton>
            </li>
            <li>
              <SnsButton
                $color='facebook'
                type='button'
                title='  '
              >
                <SnsSVG id='facebook' />
                <h3 className='a11y-hidden'>  </h3>
              </SnsButton>
            </li>
          </SnsList>
        </SimpleLoginWrap>
      </LoginWrap>
    </>
  );
}
